.leave-comment{
    padding-top: 20px;

    .title{
        font-size: 20px;
        font-weight: bold;
        padding: 20px 0;
    }

    .info-input{
        
        input{
            width: 500px;
            padding: 15px 20px;
            border: 1px solid var(--light-grey);
            color: var(--light-grey);
            font-size: 15px;
        }
    }
    .comment-input{
        width: 700px;
        height: 150px;
        padding: 15px 20px 110px;
        border: 1px solid var(--light-grey);
        color: var(--light-grey);
        font-size: 15px;
        margin-bottom: 30px;
    }

}